.introduction {
  height: 588px;
  background-color: #888290;
  background-image: url("./imgs/teclado.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.introduction h1 {
  padding-top: 60px;
  font-family: var(--font-title);
  font-size: 4.375rem;
  color: #fff;
}
.introduction p {
  display: inline-block;
  color: #fff;
  font-size: 30px;
  font-family: var(--font-text);
  width: 434px;
  line-height: 37px;
}

.introduction p::before {
  content: "";
  display: block;
  width: 184px;
  height: 3px;
  background-color: #fff;
  margin: 29px auto;
  margin-top: 47px;
}
.introduction p::after {
  content: "";
  display: block;
  width: 184px;
  height: 3px;
  background-color: #fff;
  margin: 29px auto;
  margin-bottom: 80px;
}

.introduction .a {
  color: var(--yellow);
  text-decoration: none;
  border: 5px solid var(--yellow);
  padding: 10px 30px;
  font-size: 30px;
  font-family: var(--font-title);
  background-color: rgba(0, 0, 0, 0.2);
  transition: border-color 0.4s, color 0.4s;
}

.introduction .a:hover {
  color: var(--orange);
  border-color: var(--orange);
}

@media (min-width: 320px) and (max-width: 599px) {
  .introduction {
    height: 440px;
  }
  .introduction h1 {
    padding-top: 50px;
    font-size: 45px;
  }
  .introduction p {
    font-size: 20px;
    width: 300px;
    line-height: 28px;
  }
  .introduction p::before {
    margin-top: 30px;
  }
  .introduction p::after {
    margin-bottom: 50px;
  }
  .introduction a.a {
    font-size: 18px;
    border: 3px solid;
  }
}
