.slogan{
    background-image: url('./imgs/lamparina.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: grid;
    text-align: center;
    justify-content: center;
    align-content: center;
    height: 415px;
}
.slogan div h3{
    font-family: var(--font-title);
    font-size: 45px;
    color: white;
    text-align: center;
    margin-bottom: 30px;
}
.slogan div h4{
    font-family: var(--font-text);
    font-size: 30px;
    text-align: center;
    color: white;
}
@media (min-width:320px) and (max-width:599px){
    .slogan{
        height: 200px
    }
    .slogan div h3{
        font-size: 25px;
        margin-bottom: 15px;
    }
    .slogan div h4{
        font-size: 20px;
    }
}