.contato {
  display: grid;
  text-align: center;
  justify-content: center;
  align-content: center;
  padding: 70px 0;
  grid-template-areas:
    "title title"
    "map contatos"
    "orcar orcar";
}

.contato-title {
  font-family: var(--font-title);
  font-size: 35px;
  margin-bottom: 70px;
  grid-area: title;
}

.contato-map {
  grid-area: map;
  width: 450px;
}

.contato-contatos {
  display: grid;
  grid-template-areas:
    "title"
    "item1"
    "item2"
    "item3"
    "item4";
  grid-area: contatos;
}
.contato h1 {
  scroll-margin: 200px;
}
.contato-logo img {
  width: 360px;
  height: auto;
}
.contato-item {
  display: flex;
  align-items: center;
  margin: 20px 50px;
}
.contato-item img {
  height: 50px;
  width: 50px;
}
.contato-item a {
  margin-left: 30px;
  font-family: var(--font-text);
  font-size: 30px;
  text-decoration: none;
  color: black;
}
.contato-orcar {
  margin-top: 70px;
  grid-area: orcar;
}
.contato-orcar a {
  color: var(--orange);
  text-decoration: none;
  border: 5px solid var(--orange);
  padding: 10px 80px;
  font-size: 30px;
  font-family: var(--font-title);
}
.contato-orcar a:hover {
  color: var(--yellow);
  border-color: var(--yellow);
}

@media (min-width: 320px) and (max-width: 1399px) {
  .contato {
    grid-template-areas:
      "title"
      "contatos"
      "map"
      "orcar";
    text-align: center;
  }
  .contato-map {
    width: 100%;
    height: 400px;
  }
  .contato-contatos {
    margin-bottom: 50px;
    width: 100%;
  }
  .contato-item {
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: auto;
  }
  .contato-item img {
    margin-bottom: 25px;
  }
  .contato-item a:nth-child(0),
  .contato-item a:nth-child(1) {
    margin-left: -9px;
  }
}
@media (min-width: 320px) and (max-width: 599px) {
  .contato {
    justify-content: center;
    text-align: center;
  }
  .contato-title {
    font-size: 25px;
  }
  .contato-logo img {
    width: 300px;
  }
  .contato-item a {
    font-size: 25px;
  }
  .contato-map {
    height: 300px;
    margin-right: -10px;
  }
  .contato-orcar a {
    font-size: 18px;
    border: 3px solid;
    padding: 10px 20px;
  }
}
