.orcamento {
  background-image: url("./imgs/hello.jpg");
  background-position: left;
  background-repeat: no-repeat;
  padding: 40px 0;
  display: grid;
}
.orcamento-forms {
  background-color: var(--gray);
  opacity: 0.7;
  display: grid;
  justify-content: center;
  border-radius: 40px;
  text-align: left;
  width: 600px;
  margin-left: 8%;
  padding: 40px 10px;
}
.orcamento-forms form {
  margin-left: auto;
}
.orcamento-forms label {
  font-family: var(--font-title);
  text-transform: uppercase;
  display: block;
  color: white;
  font-size: 30px;
  margin: 10px 0;
}
.orcamento-forms input {
  height: 50px;
  color: snow;
}
.orcamento-forms input:focus {
  outline: none;
}
.orcamento-forms input,
.orcamento-forms textarea {
  min-width: 200px;
  width: 500px;
  max-width: 500px;
  display: block;
  background-color: transparent;
  color: snow;
  border-radius: 5px;
  border-bottom-right-radius: 40px;
  font-size: 28px;
}
.orcamento-forms textarea {
  height: 181px;
}
.orcamento-forms textarea:focus {
  outline: none;
}
.amarelo {
  border: 3px solid var(--yellow);
}
.vermelho {
  border: 3px solid var(--orange);
}
.botao-orcamento {
  height: 65px;
  color: white;
  text-transform: uppercase;
  font-family: var(--font-title);
  font-size: 35px;
  width: 268px;
  justify-self: left;
  margin: 10px 0;
}
.botao-orcamento:hover {
  color: var(--yellow);
  border-color: var(--yellow);
}
@media (min-width: 320px) and (max-width: 722px) {
  .orcamento {
    justify-content: center;
  }
  .orcamento-forms {
    max-width: 300px;
    margin: auto 0px;
  }
  .orcamento-forms label {
    font-size: 20px;
  }
  .orcamento-forms input {
    max-width: 250px;
    max-height: 25px;
    font-size: 15px;
  }
  .orcamento-forms textarea {
    max-width: 250px;
    max-height: 100px;
    font-size: 15px;
  }
}
@media (min-width: 1920px) {
  .orcamento {
    background-size: cover;
  }
}
