.footer {
  background-color: var(--gray);
  text-align: center;
  display: flex;
  justify-content: center;
  padding-top: 70px;
  width: 100%;
}
.footer-1 {
  width: 1100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 70px;
}
.footer-sobre {
  width: 568px;
  grid-area: sobre;
  margin-left: -400px;
  text-align: center;
}
.footer-sobre img {
  width: 404px;
}
.footer-sobre p {
  font-family: var(--font-text);
  font-size: 1.625em;
  color: white;
  margin: 50px 0 70px 0;
}
.footer-sobre a {
  color: var(--yellow);
  text-decoration: none;
  font-family: var(--font-title);
  font-size: 30px;
  border: 5px solid var(--yellow);
  padding: 10px 50px;
}
.footer-sobre a:hover {
  color: var(--orange);
  border-color: var(--orange);
}

.footer-redes-sociais {
  display: grid;
  grid-template-areas:
    'title title title'
    'linkedin whatsapp facebook'
    'instagram github telegram';
  width: 40px;
  margin-left: 50px;
}
.footer-redes-title {
  grid-area: title;
  margin: 10px 0 50px 0;
}
.footer-redes-title h2 {
  color: white;
  font-family: var(--font-title);
  font-size: 35px;
}
.footer-item1 {
  grid-area: linkedin;
}
.footer-item2 {
  grid-area: whatsapp;
}
.footer-item3 {
  grid-area: facebook;
}
.footer-item4 {
  grid-area: instagram;
}
.footer-item5 {
  grid-area: github;
}
.footer-item6 {
  grid-area: telegram;
}

.footer-redes-sociais div img {
  height: 72px;
  width: 72px;
  margin: 20px;
}
.copy {
  background-color: var(--back-color);
  padding: 20px 0;
  text-align: center;
}

@media (min-width: 320px) and (max-width: 1106px) {
  .footer-1 {
    flex-direction: column;
    max-width: 320px;
  }
  .footer-sobre img {
    width: 300px;
  }
  .footer-sobre p {
    margin-top: 30px;
    font-size: 1.2em;
    width: 300px;
    margin-left: 130px;
    text-align: center;
    margin-bottom: 50px;
    margin-left: -10px;
  }
  .footer-sobre a {
    font-size: 18px;
    border: 3px solid;
    padding: 10px 50px;
  }
  .footer-sobre {
    margin: 0;
    padding: 0;
    width: 300px;
    align-content: center;
  }
  .footer-redes-sociais {
    margin: 50px 0 0 0;
    width: 300px;
  }
  .copy {
    font-size: smaller;
  }
  .footer-redes-sociais h2 {
    font-size: 25px;
  }
  .footer-redes-sociais div img {
    width: 60px;
  }
}
