.portfolio {
  background-image: url("./imgs/disco-vinil.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: grid;
  justify-content: center;
  align-content: center;
  text-align: center;
  padding: 70px 0;
  grid-gap: 0;
  grid-template-areas:
    "title"
    "trabalhos"
    "buttons"
    "orcar";
}

.portfolio h1 {
  scroll-margin: 200px;
}
.portfolio-title {
  color: #fff;
  font-family: var(--font-title);
  font-size: 35px;
  grid-area: title;
  margin-bottom: 70px;
}
.portfolio-trabalhos {
  display: grid;
  align-items: center;
  grid-template-areas: "left gallery right";
  grid-area: trabalhos;
  margin-bottom: 50px;
}
.portfolio-gallery {
  display: grid;
  grid-template-areas:
    "port1 port2"
    "port3 port4";
  grid-gap: 20px;
  justify-content: center;
  align-items: center;
  max-width: 1000px;
  grid-area: gallery;
}
.portfolio-left-arrow {
  grid-area: left;
}
.portfolio-right-arrow {
  grid-area: right;
}
.arrow:hover {
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
  transition: all 0.5s;
}
.portfolio-image-1 {
  grid-area: port1;
}
.portfolio-image-2 {
  grid-area: port2;
}
.portfolio-image-3 {
  grid-area: port3;
}
.portfolio-image-4 {
  grid-area: port4;
}
.port-imgs img {
  height: auto;
  width: 490px;
}

.portfolio-buttons {
  margin-bottom: 60px;
  grid-area: buttons;
}
.portfolio-buttons button {
  padding: 9px;
  background-color: inherit;
  border: 2px solid black;
  border-radius: 20px;
  margin: 0 5px;
}
.portfolio-buttons .portfolio-btn-ativo {
  background-color: var(--yellow);
}
.portfolio-buttons button:hover {
  background-color: var(--yellow);
}
.portfolio-orcar {
  grid-area: orcar;
}
.portfolio-orcar a {
  color: var(--yellow);
  text-decoration: none;
  font-family: var(--font-title);
  font-size: 30px;
  border: 5px solid var(--yellow);
  padding: 10px 50px;
}
.portfolio-orcar a:hover {
  color: var(--orange);
  border-color: var(--orange);
}

@media (min-width: 320px) and (max-width: 599px) {
  .portfolio {
    padding: 50px 0;
  }
  .portfolio-title {
    font-size: 25px;
    margin-bottom: 50px;
  }
  .port-imgs img {
    height: 125px;
    width: 211px;
  }
  .portfolio-gallery {
    grid-template-areas:
      "port1"
      "port2"
      "port3"
      "port4";
  }
  .portfolio-orcar a {
    font-size: 18px;
    border: 3px solid;
    padding: 10px 20px;
  }
}

@media (min-width: 600px) and (max-width: 799px) {
  .port-imgs img {
    height: 125px;
    width: 211px;
  }
}
@media (min-width: 800px) and (max-width: 1025px) {
  .port-imgs img {
    height: 175px;
    width: 295px;
  }
}
@media (min-width: 1026px) and (max-width: 1238px) {
  .port-imgs img {
    height: 230px;
    width: 388px;
  }
}
