@import url(https://fonts.googleapis.com/css2?family=Tomorrow:wght@600&display=swap);
:root{
  --gray: #292A2D;
  --orange: #FC5D5C;
  --yellow: #FEF952;
  --back-color: #E0E0E0;
  --font-title: 'Tomorrow', sans-serif;
  --font-text: 'Times New Roman', Times, serif;
}
body{
  background-color: #E0E0E0;
  background-color: var(--back-color);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
/*CONTAINER DO HEADER*/

.header {
  background-color: var(--gray);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 30;
}

/*LOGO DO HEADER*/

.header-logo {
  margin-left: 50px;
}
.header-logo img {
  height: 150px;
}

/*MENU DO HEADER*/

.header-menu {
  margin-right: 50px;
}
.header-menu nav {
  justify-content: right;
}
.header-menu nav a {
  /*Parte de animação */
  display: inline;
  background: linear-gradient(90deg, #fc5d5c, #fef952);
  background-size: 0% 3px;
  transition: background 0.3s linear;
  background-position: 0% 100%;
  background-repeat: no-repeat;

  /*Formatação Padrão */
  font-family: var(--font-title);
  margin-left: 50px;
  text-decoration: none;
  color: white;
  font-size: 30px;
}
.header-menu nav a:hover {
  background-size: 100% 3px;
}

/* HEADER HAMBURGUER */

.header-hamburguer {
  background-color: var(--gray);
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  z-index: 3;
  display: none;
  top: 0;
  grid-template-areas:
    'logo menu'
    'itens itens';
  justify-content: space-between;
  align-items: center;
}

.header-hamburguer ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: var(--gray);
  grid-area: itens;
  text-align: right;
  height: auto;
}

.header-hamburguer li a {
  display: block;
  padding: 20px 20px;
  border-right: 1px solid #f4f4f4;
  text-decoration: none;
  color: white;
  font-family: var(--font-title);
  text-transform: uppercase;
  font-size: 20px;
}

.header-hamburguer li a:hover,
.header-hamburguer .menu-btn:hover {
  background-color: #f4f4f4;
}

.header-hamburguer .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px;
  text-decoration: none;
}

/* menu */

.header-hamburguer .menu {
  clear: both;
  max-height: 0;
  transition: max-height 0.2s ease-out;
}

/* menu icon */

.header-hamburguer .menu-icon {
  cursor: pointer;
  display: inline-block;
  padding: 28px 20px;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header-hamburguer .menu-icon .navicon {
  background: white;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 40px;
  height: 5px;
}

.header-hamburguer .menu-icon .navicon:before,
.header-hamburguer .menu-icon .navicon:after {
  background: white;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header-hamburguer .menu-icon .navicon:before {
  top: 15px;
}

.header-hamburguer .menu-icon .navicon:after {
  top: -15px;
}

/* menu btn */

.header-hamburguer .menu-btn {
  display: none;
}

.header-hamburguer .menu-btn:checked ~ .menu {
  max-height: 240px;
}

.header-hamburguer .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.header-hamburguer .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header-hamburguer .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header-hamburguer .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header-hamburguer .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}
@media (max-width: 1200px) {
  .header {
    display: none;
  }
  .header-hamburguer {
    display: grid;
  }
  .header-logo {
    margin-left: 10px;
  }
  .header-logo img {
    height: 5rem;
  }
}

